<template>
  <el-card>
    <el-button @click="$success('功能建设中...')" type="primary" icon="el-icon-circle-plus-outline">新增</el-button>
    <el-divider />
    <el-table :data="seckillList" style="width: 100%">
      <el-table-column prop="id" label="ID" min-width="80"></el-table-column>
      <el-table-column prop="title" label="标题" min-width="180"></el-table-column>
      <el-table-column prop="begintime" label="开始时间" min-width="180">
        <template v-slot="$props">{{$props.row.begintime|dateFmt}}</template>
      </el-table-column>
      <el-table-column prop="endtime" label="结束时间" min-width="180">
        <template v-slot="$props">{{$props.row.endtime|dateFmt}}</template>
      </el-table-column>
      <el-table-column prop="status" label="状态" min-width="180">
        <template v-slot="$props">
          <el-tag type="success" v-if="$props.row.status===1">正常</el-tag>
          <el-tag type="danger" v-else>禁用</el-tag>
        </template>
      </el-table-column>
      <el-table-column label="操作" min-width="180">
        <template v-slot="$props">
          <el-button
            @click="edit($props.row)"
            type="success"
            circle
            icon="el-icon-edit"
            size="mini"
          ></el-button>
          <el-button
            @click="del($props.row.uid)"
            type="danger"
            circle
            icon="el-icon-delete"
            size="mini"
          ></el-button>
        </template>
      </el-table-column>
    </el-table>
  </el-card>
</template>

<script>
import { mapState, mapActions, mapMutations } from "vuex";
export default {
  filters: {
      dateFmt(time){
          return new Date(parseInt(time)).toLocaleDateString();
      }
  },
  data() {
    return {
      seckillList: []
    };
  },
  created() {
    this.getSeckillList();
  },
  computed: {
    ...mapState({
      adminList: state => state.admin.adminList,
      adminTotal: state => state.admin.adminTotal
    })
  },
  methods: {
    getSeckillList() {
      this.$http.get("/secklist").then(data => {
        if (data.code === 200) {
          this.seckillList = data.list;
        }
      });
    },
    edit() {
      this.$success("功能建设中...");
    },
    del() {
      this.$success("功能建设中...");
    }
  }
};
</script>

<style>
</style>


